.awesome { 
    font-weight: 800;           
    color:#dbb9a3;
    -webkit-animation:colorchange 8s infinite;
    animation: colorchange 8s infinite;
}

@-webkit-keyframes colorchange {
      0% {
        
        color: #001dff;
      }
      
      10% {
        
        color: #b200ff;
      }
      
      20% {
        
        color: #ff00e1;
      }
      
      30% {
        
        color: #b200ff;
      }
      
      40% {
        color: #00faff;
      }
      
      50% {
        color: #00ff61;
      }
      
      60% {
        color: #2eff00;
      }
      
      70% {
        
        color: #d0ff00;
      }
      80% {
     
        color: #b200ff;
      }
      
      90% {
     
        color: #ff4300;
      }
      
      100% {
        
        color: #009dff;
      }
}
